.uploadPhotoBtn {
    width: 22px;
    height: 22px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    background: url('./icons/photo-camera.svg') center / cover no-repeat ;
    margin: 0 25px 0 0;
    input {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        width: 100%;
        opacity: 0;
        cursor: pointer;
        padding-top: 100%;
    }
}