
.post {
	display: flex;
	flex-flow: column;
	padding: 20px 20px 15px;
	position: relative;
	background-color: #fff;
	margin-bottom: 10px;
	border-radius: 4px;
	&:last-child {
		margin-bottom: 0;
	}
	&:first-child {
		border-radius: 0px;
		border-bottom-left-radius: 4px;
		border-bottom-right-radius: 4px;
	}
	&:hover {
		.delete {
			opacity: 1;
			visibility: visible;
		}
	}
}
.head {
	display: flex;
	align-items: center;
	margin-bottom: 8px;
}
.right {
	display: flex;
	flex-flow: column;
	justify-content: space-between;
	height: calc(100% - 13px);
	a {
		cursor: pointer;
		text-decoration: none;
	}
}
.avatar {
	width: 50px;
	height: 50px;
	margin-right: 13px;
	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
		border-radius: 50%;
	}
}
.name {
	font-weight: 500;
	color: #2a5885;
	width: max-content;
	font-size: 13px;
	&:hover {
		text-decoration: underline;
	}
}
.link {
	color: #edeef0;
}

.message {
	border-bottom: 1px solid #edeef0;
	padding: 0 4px 10px;
	img {
		max-width: 610px;
		max-height: 510px;
		object-fit: cover;
		display: block;
	}
}
.postText {
	margin-bottom: 21px;
	overflow: hidden;
	padding-top: 3px;
}

.footer {
	position: relative;
	padding: 15px 0 0;
}
.likes {
	display: flex;
}
.like {
	width: 24px;
	height: 24px;
	background: transparent;
	border: none;
	display: block;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 24px;
		height: 24px;
		background: url('../Post/icons/heart-trans.svg') center / cover no-repeat;
	}
}
.like_active {
	width: 24px;
	height: 24px;
	background: transparent;
	border: none;
	display: block;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 24px;
		height: 24px;
		background: url('../Post/icons/heart.svg') center / cover no-repeat;
	}
}
.number {
	font-size: 14px;
	font-weight: 500;
	color: #626d7a;
	&_active {
		color: #D7443E;
	}
}
.time {
	color: #626d7a;
	font-size: 12.5px;
}
.delete {
    position: absolute;
    right: 15px;
    top: 15px;
    width: 20px;
    height: 20px;
	cursor: pointer;
	opacity: 0;
	visibility: hidden;
}