.posts {
    display: flex;
    flex-flow: column;
}
.postsHeader {
    display: flex;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom: 1px solid #dce1e6;
    background: #fff;
    padding: 0 10px;
    position: relative;
    &__item {
        padding: 13px 10px;
        background-color: #fff;
        border-bottom: 2px solid #5181b8;
        font-size: 13px;
        cursor: pointer;
    }
}
.postsWrapper {
    background: #edeef0;
}
.notEnough {
    background: #fff;
    width: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}
.notEnoughWrap {
    padding: 36px 20px;
}
.notEnoughText {
    color: #656565;
    font-size: 13px;
}
.notEnoughPic {
    img {
        margin: 0 auto 15px;
        display: block;
    }
}