.status {
    position: relative;
}
.editor {
    position: absolute;
    padding: 15px;
    background: #fff;
    z-index: 4;
    width: 100%;
    border: 1px solid #d3d9de;
    background: #fafbfc;
    display: flex;
    flex-flow: column;
    left: -13px;
    box-shadow: 0 1px 3px #0000001f;
    input {
        padding: 5px;
        border: 1px solid #d3d9de;
        margin-bottom: 10px;
    }
    button {
        width: max-content;
    }
}
.statusSpan {
    width: 100%;
    padding: 2px;
    cursor: pointer;
    &:hover {
        background: #aeb7c21f;
    }
    span {
        width: 100%;
        display: block;
    }
}